                  <a-form-model-item label="命令">
                    <a-select v-model="form.commandId" placeholder="请选择命令" @change="handleChange()">
                      <a-select-option v-for="(item, index) in commandList" :value="item.id" :key="index">
                        {{item.commandLabel}}
