           <!--    只能输入O和1的参数  v-if="deviceObj.deviceStatus === 'ONLINE'"      -->
            <a-tab-pane key="2" tab="设备模拟器">
              <div class="pane-content">
                <a-button type="primary" @click="submitMessage">属性上报</a-button>
                <div class="tab-text">
                  <span>设备模拟器可根据产品定义向平台上报数据。</span>
