        </div>
        <div ref="drawerContainer" class="drawerContainer"></div>
      </div>
    </div>
    </div>
  <a-drawer
    width="100%"
    :mask="true"
    title="消息跟踪"
    placement="left"
    :closable="true"
    :visible="visible"
    :get-container="() => {return $refs.drawerContainer}"
    :wrap-style="{ position: 'absolute' , overflowY: 'scroll', overflowX: 'hidden'}"
    @close="onClose"
  >
    <a-list item-layout="vertical"  :data-source="messageData">
      <a-list-item slot="renderItem" :key="index" slot-scope="item, index">
        <a-list-item-meta>
           <span slot="title" >
          <span class="mes-title" v-for="(step, index) in stepArr" :key="index" v-if="step.value === item.businessStep">
             [{{step.label}}]
          </span>
             <span class="mes-subTime">{{item.createTime}}</span>
        </span>
        </a-list-item-meta>
        {{ item }}
      </a-list-item>
    </a-list>
  </a-drawer>
  <a-drawer
    width="1000"
    title="选择设备"
    placement="right"
    :closable="true"
    :visible="devVisible"
    @close="onDevClose"
  >
    <div>
      <div>
        <a-select v-model="searchObj.key" style="width: 100px">
          <a-select-option value="deviceLabel">
            设备名称
          </a-select-option>
          <a-select-option value="deviceCode">
            设备标识码
          </a-select-option>
          <a-select-option value="id">
            设备ID
          </a-select-option>
        </a-select>
        <a-input-search v-model="searchObj.val" allowClear placeholder="请搜索" style="width: 200px" @search="onSearch" />
      </div>
      <div style="margin-top: 24px;">
        <a-table
          :rowKey="(record, index) => {return record.id}"
          :row-selection="{ selectedRows: selectedRows, onChange: onSelectChange, type: 'radio'}"
          :pagination="pagination"
          :columns="columns"
          @change="tableChange"
          :data-source="data">
            <span slot="deviceStatus" slot-scope="text">
            <span v-for="(item, index) in stateArr" :key="index" v-if="item.value === text">
              <span class="device-status" :style="{background: item.color}"></span>
              {{item.label}}
            </span>
          </span>
        </a-table>
      </div>
    </div>
    <div
      :style="{
          position: 'absolute',
          left: 0,
          bottom: 0,
          width: '100%',
          borderTop: '1px solid #e9e9e9',
          padding: '10px 16px',
          background: '#fff',
          textAlign: 'left',
          zIndex: 1
        }"
    >
      <a-button type="primary" @click="onDevSubmit">
        提交
      </a-button>
      <a-button :style="{ marginRight: '8px' }" @click="onDevClose">
        取消
      </a-button>

    </div>
  </a-drawer>
  </div>
</template>

<script>
import CodeEdit from '../../components/CodeEdit/CodeEdit'
const columns = [
  {
    dataIndex: 'deviceStatus',
    title: '状态',
    ellipsis: true,
    key: 'deviceStatus',
    scopedSlots: { customRender: 'deviceStatus' }
  },
  {
    dataIndex: 'deviceLabel',
    title: '设备名称',
    ellipsis: true,
    key: 'deviceLabel'
  },
  {
    title: '设备标识码',
    dataIndex: 'deviceCode',
    ellipsis: true,
    key: 'deviceCode'
  },
  {
    title: '所属产品',
    key: 'productLabel',
    ellipsis: true,
    dataIndex: 'productLabel'
  }
]
export default {
  name: 'onlineDebug',
  components: {
    CodeEdit
  },
  data () {
    return {
      searchObj: {
        key: 'deviceLabel',
        val: ''
      },
      devForm: {
        message: ''
      },
      devRules: {
        message: [
          { validator: this.$validator.checkNumberVal, message: '请输入十六进制参数', trigger: ['blur', 'change'] }
        ]
      },
      listdata: [],
      listRightData: [],
      stateArr: [
        {
          value: 'ONLINE',
          color: '#15C937',
          label: '在线'
        },
        {
          value: 'WARN',
          color: '#C91521',
          label: '告警'
        },
        {
          value: 'INACTIVE',
          color: '#beccfa',
          label: '未激活'
        },
        {
          value: 'OFFLINE',
          color: '#C8421B',
          label: '离线'
        },
        {
          value: 'STOP',
          color: '#C8A01B',
          label: 'STOP'
        }
      ],
      stepArr: [
        {
          value: 'DEVICE_REPORT_DATA',
          label: '设备上报数据'
        },
        {
          value: 'PLATFORM_RESOLVING_DATA',
          label: '平台解析数据'
        },
        {
          value: 'PLATFORM_RESOLVING_DATA_ERROR,',
          label: '平台解析数据错误'
        },
        {
          value: 'MATCH_MODEL',
          label: '匹配物模型'
        },
        {
          value: 'PLATFORM_SEND_COMMAND',
          label: '平台发送命令'
        },
        {
          value: 'PLATFORM_SEND_DATA2MQ',
          label: '平台发送数据到消息队列'
        }
      ],
      devVisible: false,
      visible: false,
      labelCol: { span: 4 },
      wrapperCol: { span: 20 },
      form: {
        deviceId: '',
        commandId: undefined,
        params: {},
        modelId: undefined
      },
      data: [],
      columns,
      selectedRows: [],
      loading: false,
      pagination: {
        showLessItems: true,
        pageSizeOptions: ['10', '20', '30'],
        showSizeChanger: true,
        pageSize: 10,
        current: 1,
        hideOnSinglePage: false,
        total: 0
      },
      chooseVal: 'DEVICE_REPORT_DATA',
      commandList: [],
      paramsArr: [],
      modelsArr: [],
      isParams: false,
      deviceObj: {},
      message: '',
      messageData: [],
      checked: true,
      timer: '',
      productObj: {},
      numLength: 0
    }
  },
  watch: {
    'checked': {
      handler() {
        console.log('监听', this.checked)
        if (this.checked) {
          this.getInter()
        } else {
          console.log('执行难')
          clearInterval(this.timer)
          this.timer = null
        }
      }
    }
  },
  computed: {
    hasSelected() {
      return this.selectedRowKeys.length > 0
    }
  },
  activated() {
    if (this.$route.query.deviceObj) {
      this.deviceObj = JSON.parse(this.$route.query.deviceObj)
      console.log('设备调试', this.deviceObj)
      if (this.deviceObj) {
        this.getMessageInfo()
        this.debugs()
        this.getProductCommand()
        this.handleChange()
      }
    }
  },
  mounted() {
    if (this.$route.query.deviceObj) {
      this.deviceObj = JSON.parse(this.$route.query.deviceObj)
      console.log('设备调试', this.deviceObj)
      if (this.deviceObj) {
        this.getMessageInfo()
        this.debugs()
        this.getProductCommand()
        this.handleChange()
      }
      if (this.checked) {
        this.getInter()
      }
    }
  },
  methods: {
    getInter() {
      this.timer = setInterval(() => {
        this.getMessageInfo()
      }, 5000)
      this.$once('hook:beforeDestroy', () => {
        clearInterval(this.timer)
        this.timer = null
      })
    },
    /**
     * 切换分页
     * */
    tableChange(pagination) {
      this.pagination.pageSize = pagination.pageSize
      this.pagination.current = pagination.current
      this.pagination.total = pagination.total
      this.getList()
    },
    /**
     * 获取设备列表
     * */
    getList() {
      const pageOption = {
        searchKey: this.searchObj.key,
        searchValue: this.searchObj.val,
        deviceStatus: '',
        page: this.pagination.current,
        pageSize: this.pagination.pageSize
      }
      this.$api.getDeviceLis(pageOption).then(res => {
        const resData = res.data
        console.log('设备数据', res)
        this.pagination.total = resData.total
        this.pagination.pageSize = resData.size
        this.pagination.current = resData.current
        const resRecords = resData.records
        this.data = resRecords
      })
    },
    /**
     * 获取应用模拟器追踪消息列表
     * */
    getMessageInfo() {
      const obj = {
        businessType: '',
        debugDevice: true
      }
      this.$api.getDeviceMessage(this.deviceObj.id, obj).then(res => {
        console.log('消息追踪信息', res.data)
        let mesData = res.data.reverse()
        mesData.map(item => {
          Object.assign(item, {
            createTime: this.$getDate({timestamp: item.createTime})
          })
          let itemObj = {}
          itemObj = item
          this.messageData.push(itemObj)
          if (item.businessType === 'DEVICE2PLATFORM') {
            let listObj = {}
            listObj = item
            this.listRightData.push(listObj)
          } else {
            let RightObj = {}
            RightObj = item
            this.listdata.push(RightObj)
            console.log('数组', this.listdata)
          }
        })
        let resData = this.messageData
        this.messageData = this.setArrData(resData)
        let resRightListData = this.listRightData
        this.listRightData = this.setArrData(resRightListData)
        let resListData = this.listdata
        this.listdata = this.setArrData(resListData)
        console.log('listData', this.listdata)
        this.numLength = this.listdata.length + this.listRightData.length
      })
      this.getProduct()
    },
    setArrData(arr) {
      const res = new Map()
      return arr.filter((arr) => !res.has(arr.id) && res.set(arr.id, 1))
    },
    /**
     * 获取当前设备所属产品的命令列表
     * */
    getProductCommand() {
      this.$api.getProductInfo(this.deviceObj.productId).then(res => {
        const productCommand = res.data.models
        console.log('设备产品', res)
        let commandArr = []
        this.form.commandId = undefined
        this.form.modelId = productCommand[0].id
        if (productCommand[0].commands.length > 0) {
          this.form.commandId = productCommand[0].commands[0].id
          commandArr = productCommand[0].commands[0].params
        }
        console.log('commanID', this.form.commandId)
        this.modelsArr = productCommand
        if (productCommand.length > 0) {
          productCommand.map(item => {
            item.commands.map(commandItem => {
              if (commandItem.id === this.form.commandId) {
                let commandObj = {}
                Object.assign(commandObj, {
                  id: commandItem.id,
                  commandLabel: commandItem.commandLabel
                })
                this.commandList.push(commandObj)
              }
            })
          })
          this.commandList = this.setArrData(this.commandList)
          console.log('命令列表', this.commandList)
        }
        if (commandArr.length > 0) {
          this.isParams = true
          commandArr.map(item => {
            let params = {}
            Object.assign(params, {
              key: item.paramLabel,
              value: ''
            })
            this.paramsArr.push(params)
            console.log('参数列表', this.paramsArr)
          })
        }
      })
    },
    /**
     * 选择框
     * */
    handleChange() {
      console.log('6333', this.commandList)
      this.paramsArr = []
      let commandArr = []
      this.commandList.map(item => {
        if (item.id === this.form.commandId) {
          commandArr.push(item)
          this.isParams = true
        }
      })
      commandArr.map(commandItem => {
        if (commandItem.params && commandItem.params.length > 0) {
          commandItem.params.map(item => {
            let params = {}
            Object.assign(params, {
              key: item.paramLabel,
              value: ''
            })
            this.paramsArr.push(params)
          })
        }
      })
    },
    /**
     * 提交下发命令
     * */
    submitDev() {
      this.form.deviceId = this.deviceObj.id
      let commandobj = {}
      this.paramsArr.map(items => {
        commandobj[items.key] = items.value
        console.log('键值对', commandobj)
      })
      this.form.params = commandobj
      console.log('提交的表单', this.form)
      this.$refs.ruleForm.validate(valid => {
        if (valid) {
          this.$api.sendDeviceCommand(this.form).then(res => {
            this.visible = false
            this.getMessageInfo()
          })
        }
      })
    },
    /**
     * 属性上报
     * */
    submitMessage() {
      let messageObj = {
        deviceId: this.deviceObj.id,
        message: this.devForm.message
      }
      if (this.productObj.dataFormat !== 'BINARY') {
        messageObj.message = this.$refs.codeEdit.getValue()
      }
      this.$api.postFakeMessage(messageObj).then(res => {
        console.log('属性上报', res)
        this.getMessageInfo()
      })
    },
    onSelectChange(selectedRowKeys, selectedRows) {
      console.log('selectedRowKeys changed: ', selectedRows)
      this.selectedRows = selectedRows
    },
    showDrawer() {
      this.visible = true
      this.getMessageInfo()
    },
    onClose() {
      this.visible = false
    },
    onChange(vale) {
      console.log('changed', vale.target.checked)
      this.checked = vale.target.checked
      console.log('this.checked', this.checked)
    },
    showDevDrawer() {
      this.devVisible = true
      this.getList()
    },
    onDevClose() {
      this.devVisible = false
    },
    onDevSubmit() {
      this.deviceObj = this.selectedRows[0]
      this.devVisible = false
      this.listdata = []
      this.listRightData = []
      this.messageData = []
      this.getMessageInfo()
      this.debugs()
      this.getProductCommand()
      this.handleChange()
    },
    chooseMessage() {
      const message = []
      this.listdata.map(item => {
        if (item.businessType === this.chooseVal) {
          message.push(item)
        }
      })
      this.listdata = message
    },
    onSearch() {
      this.getList()
    },
    /**
     * 清除日志
     * */
    clearData() {
      this.listdata = []
      this.listRightData = []
      this.messageData = []
      this.numLength = 0
    },
    /**
     * debug
     * */
    debugs() {
      this.$api.getDebug(this.deviceObj.id).then(res => {
        console.log('debug', res)
      })
    },
    /**
     * 获取产品详情
     * */
    getProduct() {
      this.$api.getProductDetails(this.deviceObj.productId).then(res => {
        console.log('产品详情res', res)
        this.productObj = res.data
      })
    }
  }
}
</script>

<style lang="scss" scoped>
.debug-full {
  height: calc(100vh - 180px);
  min-height: 40rem;
  margin: 24px;
  .debug-content {
    height: 100%;
    box-shadow: 0 0 20px rgba(0,0,0,0.1);
    .debug-tool {
      height: 100%;
      border-top: 1px solid #e5e5e5;
      background: #eef0f5 !important;
      padding-left: 1.5rem;
      position: relative;
      .debug-bar {
        height: 3.5rem;
        .debugger-content-text {
          font-size: 14px;
          color: #252b3a;
          line-height: 20px;
          font-weight: 600;
        }
      }
    }
  }
}
.online-title {
  height: 5rem;
  background: #eef0f5!important;
  .app-sim-title-container {
    width: 25%;
    background: #fff;
    float: left;
    height: 100%;
    padding-left: 1.25rem;
    font-size: 16px;
    color: #252b3a;
    line-height: 4rem;
    font-weight: 600;
  }
  .app-to-platform {
    width: 12.5%;
    float: left;
    height: 100%;
    }
  .platform-container {
    width: 25%;
    background: #fff;
    float: left;
    height: 80%;
    .platform-title-container {
      text-align: center;
      cursor: pointer;
      .platform-title-text {
        font-size: 16px;
        color: #252b3a;
        line-height: 4rem;
        font-weight: 600;
        &:hover {
          color: #0456de;
        }
      }
      .platform-message-title-text {
        vertical-align: text-top;
        border-radius: 100%;
        background-color: #f66f6a;
        color: #fff;
        float: right;
        position: relative;
        top: -0.5rem;
        right: -0.5rem;
      }
    }
  }
  .dev-sim-container {
    width: 25%;
    background: #fff;
    float: left;
    height: 100%;
    font-size: 16px;
    color: #252b3a;
    line-height: 4rem;
    font-weight: 600;
  }
  .platform-to-dev {
    width: 12.5%;
    float: left;
    height: 100%;
  }
  .equipment-platform {
    position: absolute;
    display: inline-block;
    vertical-align: middle;
    width: 12.5%;
    line-height: 1;
    color: #4d4d4d;
    text-align: center;
    font-size: 12px;
    .border-dot-top {
      position: relative;
      padding-bottom: 0.6rem;
      margin-bottom: 1rem;
      border-bottom: 2px solid #999;
      &::before {
        left: 0;
        top: 100%;
        margin-top: -0.15rem;
        content: "";
        display: block;
        width: 0.4rem;
        height: 0.4rem;
        border-radius: 0.2rem;
        border: 2px solid #999;
        background-color: #ebedf0;
        position: absolute;
      }
      &::after {
        content: "";
        display: block;
        width: 0;
        height: 0;
        -webkit-transform: rotate(
            45deg
        );
        transform: rotate(
            45deg
        );
        position: absolute;
        right: 0;
        top: 97%;
        margin-top: -0.15rem;
        border-color: #999 #999 transparent transparent;
        border-style: solid;
        border-width: 4px;
      }
    }
    .border-dot-bottom {
      border-color: #ccc;
      position: relative;
      padding-top: 0.6rem;
      border-top: 2px dashed #999;
      &::before {
        content: "";
        display: block;
        width: 0;
        height: 0;
        -webkit-transform: rotate(
            -135deg
        );
        transform: rotate(
            -135deg
        );
        position: absolute;
        top: -11%;
        margin-top: -0.15rem;
        border-color: #999 #999 transparent transparent;
        border-style: solid;
        border-width: 4px;
      }
      &::after {
        right: 0;
        top: -1px;
        margin-top: -0.15rem;
        content: "";
        display: block;
        width: 0.4rem;
        height: 0.4rem;
        border-radius: 0.2rem;
        border: 2px solid #999;
        background-color: #ebedf0;
        position: absolute;
      }
    }
  }
  }
.debug-box {
  display: flex;
  height: calc(100vh - 200px - 8.5rem);
  min-height: 31.5rem;
  width: 100%;
  background: #ffffff;
  .debugger-center {
    height: 100%;
    width: 49%;
    padding: 24px;
    overflow-y: scroll;
    overflow-x: hidden;
  }
  .debug-border {
    width: 2%;
    height: 100%;
    background: #eef0f5 !important;
  }
  .south-equipment-box {
    height: 100%;
    width: 50%;
    padding: 24px;
    overflow-y: scroll;
    overflow-x: hidden;
  }
}
.drawerContainer {
  height: 32rem;
  width: 61%;
  position: absolute;
  bottom: 0;
}
.app-sim-tab {
  width: calc(38% - 1.5rem);
  margin-left: 1.5rem;
  float: left;
  height: 100%;
  background: #ffffff;
  padding: 0 1.5rem;
  .pane-content {
    margin-top: 0;
  }
  .tab-text {
    padding-top: 0.75rem;
    font-size: 12px;
    color: #8a8e99;
    line-height: 24px;
    margin-bottom: 20px;
  }
  .dev-btn {
    margin-top: 24px;
    display: flex;
    justify-content: space-between;
  }
}
.debug-head {
  display: flex;
  justify-content: space-between;
  background: #ffffff;
  .debug-head-btn {
    display: flex;
    span {
     // width: 80px;
      text-align: center;
      padding-top: 24px;
      padding-right: 10px;
    }
    button {
      margin-top: 14px;
      margin-right: 20px;
    }
  }
}
.debugger-list {
  margin-left: 20px;
}
.descripText {
  word-wrap:break-word;
  word-break:normal;
  color: rgba(0,0,0,.45);
  font-size: 14px;
  line-height: 22px;
  margin-bottom: 0;
  width: 480px;
}
.home-title {
  width: 80%;
}
.mes-title {
  color: #333333;
  font-size: 16px;
  font-weight: bold;
}
.mes-img {
  margin-top: -5px;
  margin-right: 5px;
}
.mes-subTime {
  font-size: 12px;
  color: #999999;
  margin-left: 10px;
}
>>> .ant-list-vertical .ant-list-item-meta {
  margin-bottom: 0;
}
</style>



